.wizard {
  position: relative;
}
.wizard .actions {
  background: #f4f5f6;
  background-image: -webkit-linear-gradient(top, #fafafa, #eeeff1);
  background-image: linear-gradient(to bottom, #fafafa, #eeeff1);
  float: none;
  top: 0;
}
.wizard .actions .btn-next .glyphicon,
.wizard .actions .btn-prev .glyphicon {
  display: none;
}
.wizard .actions .btn-prev {
  background: none;
  border: none;
  box-shadow: none;
  color: #2168a7;
}
.wizard .actions .btn-prev:hover,
.wizard .actions .btn-prev:focus {
  color: #33638c;
}
.wizard .steps {
  background: #f4f5f6;
  background-image: -webkit-linear-gradient(top, #fafafa, #eeeff1);
  background-image: linear-gradient(to bottom, #fafafa, #eeeff1);
  *zoom: 1;
}
.wizard .steps:before,
.wizard .steps:after {
  display: table;
  content: "";
  line-height: 0;
}
.wizard .steps:after {
  clear: both;
}
.wizard .steps li {
  background: #f3f4f5;
  box-shadow: 0px 0px 0px 1px #fff inset;
}
.wizard .steps li:before {
  background: #d4d4d4;
  bottom: -1px;
  content: " ";
  display: block;
  height: 1px;
  position: absolute;
  right: -1px;
  width: 5px;
  z-index: 5;
}
.wizard .steps li.active {
  color: #4e8abe;
}
.wizard .steps li.active .badge {
  background: #4e8abe;
}
.wizard .steps li.active .chevron:before {
  border-color: transparent transparent transparent #fff;
}
.wizard .steps li.active .chevron:after {
  border-color: transparent transparent transparent #f1f6fc;
}
.wizard .steps li.complete {
  color: #5fb21c;
}
.wizard .steps li.complete .badge {
  background: #5fb21c;
  color: #5fb21c;
  position: relative;
}
.wizard .steps li.complete .badge:before {
  color: #fff;
  content: "\e013";
  display: block;
  font-family: "Glyphicons Halflings";
  font-size: 10px;
  left: 4px;
  position: absolute;
  top: 4px;
}
.wizard .steps li.complete .chevron:before {
  border-color: transparent transparent transparent #fff;
}
.wizard .steps li.complete .chevron:after {
  border-color: transparent transparent transparent #f3f4f5;
}
.wizard .steps li.complete:hover .chevron:after {
  border-color: transparent transparent transparent #e7eff8;
}
.wizard .steps li .badge {
  background: #999;
  padding: 3px 6px;
}
.wizard .steps li .chevron:before {
  border-color: transparent transparent transparent #fff;
}
.wizard .steps li .chevron:after {
  border: 24px solid transparent;
  border-left: 14px solid #f3f4f5;
  border-right: 0;
  content: "";
  display: block;
  position: absolute;
  right: 2px;
  top: -24px;
}
